.todocontainer {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 230px 0.1fr 2fr 0.1fr;
    grid-template-areas:
        "title title"
        "newtodo newtodo"
        "main main"
        "footer footer";

}